.gametype {
  /* gap: 20px; */
  cursor: pointer;
  background-color: var(--dark-color);
  /* height: 31vh; */
  /* width: 23vw; */
  padding: 4%;
  box-shadow: 5px 5px 10px var(--dark-color);
  border-radius: 5px;
}

.gametype__removedecoration {
  text-decoration: none;
  color: aliceblue;
}

.gametype__removedecoration:nth-child(2) {
  margin: 0 10px;
}

.gametype__link {
  position: relative;
  top: 3vh;
}

.gametype__image {
  height: auto;
  width: 100%;
  margin: 0;
  padding: 0;
  border-radius: 5px;
  padding: 10px;
  border: 1px solid lightblue;
}

.gametype:hover {
  transition: all 0.5s;
  transform: scale(1.25, 1.25);
  box-shadow: 15px 15px 30px var(--dark-color);
}

.gametype:hover img {
  border: 4px solid rgb(27, 173, 1);
}

.gametype:hover h3 {
  transition: all 0.5s;
  transform: scale(1.25, 1.25);
  color: rgb(145, 255, 0);
}

@media (max-width: 991px) {
  .gametype {
    width: 50vw;
  }

  .gametype__removedecoration:nth-child(2) {
    margin: 0;
  }

  .gametype__link {
    top: 1vh;
  }
}