.back {
  cursor: pointer;
  font-size: 2vw;
  position: absolute;
  top: 1vh;
  left: 2vw;
  border: none;
  background: var(--lightColor);
  transition: all .3s ease-in-out;
  border-radius: 5px;
  padding: 0.25em 0.5em;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-color);
  color: white;
}
.back-img {
  width: 2rem;
  height: 2rem;
  margin-right: 5px;
  transition: all .3s ease-in-out;
}
@media (max-width: 768px) {
  .back-img {
    width: 15px;
    height: 15px;
  }
}
.back:hover {
  scale: 1.1;
}