* {
  margin: 0;
  box-sizing: border-box;
}
html{
  overflow-x: hidden;
}
:root {
  --dark-color: rgba(34, 34, 34, 0.9);
  --bgColorBlur: rgba(233, 233, 233, 0.3);
  --lightColor: rgba(233, 233, 233, .8);
  --silver: rgba(233, 233, 233);
  --smallSpacing: 5vh;
}
#root {
  /* min-height: 100vh; */
}
@font-face {
  font-family: 'Ubuntu';
  src: url('./fonts/Ubuntu-Bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu';
  src: url('./fonts/Ubuntu-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Ubuntu';
  src: url('./fonts/Ubuntu-Medium.woff2') format('woff2');
  font-weight: medium;
  font-style: normal;
}
body {
  margin: 0 auto;
  font-family: 'Ubuntu', 'Verdana', 'sans-serif';
  font-size: 20px;
  font-weight: normal;
  /* min-height: 100vh; */
}

.screensizewarn {
  display: none;
}

@media screen and (orientation: portrait) {
  .app__gameplay {
    display: none;
  }
  .home {
    display: none;
  }
  .app{
    min-height: 100vh;
  }
  .screensizewarn {
    display: block;
  }
}

.card-enter {
  opacity: 0.01;
}

.card-enter.card-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.card-leave {
  opacity: 1;
}

.card-leave.card-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}
