.card {
  position: relative;
  border: 1px solid black;
  cursor: pointer;
  width: 6.4vw;
  height: 9.8vw;
  border-radius: 0.3vw;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: space-between;
}

.card__rank-left {
  text-align: left;
  margin-left: 0.2vw;
}

.card__rank-right {
  text-align: left;
  position: relative;
  transform: rotate(180deg);
  bottom: .2vw;
  margin-right: 0.4vw;
}

.card__suite-left {
  text-align: left;
  margin-left: 0.2vw;
}

.card__suite-right {
  text-align: left;
  position: relative;
  transform: rotate(180deg);
  bottom: .2vw;
  margin-right: 0.4vw;
}

.card__suite {
  text-align: center;
}

.card__down {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../assets/card_back.png");
  text-decoration: transparent;
  /* margin-bottom: -8.5vw; */
}

.card__down > .card__content {
  visibility: hidden;
}

.card__selected {
  box-shadow: 10px 10px 12px black !important;
  border: 2px solid black;
}

.card__remcards {
  position: absolute;
  bottom: 0;
  right: 0;
}

.card__stack {
  margin-bottom: -7vw;
  transition: all 2s smooth;
}
