.card:hover {
  box-shadow: 4px 4px 10px rgb(45, 194, 0);
}

.card__highlighted {
  box-shadow: 5px 5px 10px red !important;
  border: 2px solid red;
}

.heart {
  background: url("../assets/heart.png") var(--silver);
  color: red;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
.spade {
  background: url("../assets/spade.png") var(--silver);
  color: black;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
.club {
  background: url("../assets/club.png") var(--silver);
  color: black;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}
.diamond {
  background: url("../assets/diamond.png") var(--silver);
  color: red;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}

@media screen and (max-width: 1390px) and (min-width: 1200px) {
  .card__content {
    /* font-size: 15px; */
  }
  .card__selected {
    border: 2px solid red;
    box-shadow: none !important;
  }
  .card__highlighted {
    border: 2px solid palevioletred;
    box-shadow: none !important;
  }
}

@media screen and (max-width: 1199px) and (min-width: 781px) {
  .card {
    width: 8vw;
    height: 13vw;
  }
  .card__down {
    /* margin-bottom: -10vw; */
  }
  .card__content {
    font-size: 15px;
  }
  .card__selected {
    border: 2px solid red;
    box-shadow: none !important;
  }
  .card__highlighted {
    border: 2px solid palevioletred;
    box-shadow: none !important;
  }
}

@media (max-width: 780px) {
  .card {
    width: 7vw;
    height: 12vw;
  }
  .card__down {
    /* margin-bottom: -10vw; */
  }
  .card__content {
    font-size: 10px;
  }
  .card__selected {
    border: 2px solid red;
    box-shadow: none !important;
  }
  .card__highlighted {
    border: 2px solid palevioletred;
    box-shadow: none !important;
  }
}
